import axios from 'axios'
import CryptoJS from 'crypto-js'

// uniapp
let isUniApp = typeof uni !== 'undefined'
// sessionStorage ()
let isSupStorage = typeof sessionStorage !== 'undefined'

/**
 * 
 */
const RC = {
    SUCCESS: "succ",
    FAILURE: "fail",
    NOSESSION: "no session",
    AJAXERROR: "ajax error"
};


/**
 *  </br>
 *  
 * @param url 
 * @param content 
 * @param callback 
 */
function call(api, content, callback) {
    let url = api
    //sessionidsecret

    //
    let userSession = (isUniApp ? (function () {
        try {
            uni.getStorageSync("USER_SESSION")
        } catch (e) {}
    }) : sessionStorage.getItem(
        "USER_SESSION")) || {};

    let id = undefined;
    let secret = undefined;
    if (userSession != null) {
        id = userSession.id;
        secret = userSession.loginToken;
    }
    let req = {
        c: content
    };
    if (id != undefined && secret != undefined) {
        req.id = id;
        //id->cHmacSHA1(verifyByHmacSHA1ToID64)v
        //console.debug("verify->>>" + req.id + "&" + req.cnt + "&" + secret);
        req.v = CryptoJS.HmacSHA1((req.id + req.c), secret).toString(CryptoJS.enc.Hex);
    }
    //axioscontent-typeoptions
    //content-typetext/plain
    let data = JSON.stringify(req);

    //console.log(data);
    // console.log(":" + "------------" + data.length);

    if (isUniApp) {
        uni.request({
            url,
            data,
            header: {
                "Content-Type": "text/plain"
            },
            method: "POST",
            success(res) {
                callback(res);
            },
            fail(readyState, status, error) {
                callback({
                    rc: RC.AJAXERROR,
                    rm: (readyState + "&" + status + "&" + error)
                })
            }
        });


    } else {

        // req.setRequestHeader("Content-Type", "text/plain");//androidOPTIONS
        axios.defaults.headers = {
            "Content-Type": "text/plain"
        }

        axios.post(url, data).then(function (json) {
            // console.error("axios>" + JSON.stringify(json));
            callback(json);
        }).catch(function (readyState, status, error) {
            // console.error("axios>>>" + error);
            console.error(readyState);
            callback({
                rc: RC.AJAXERROR,
                rm: (readyState + "&" + status + "&" + error)
            })
        })
    }

}

function isBlank(str) {
    if (str == null || typeof str == "undefined" ||
        str == "" || str.trim() == "") {
        return true;
    }
    return false;
}

/**
 *@tryParseJson json
 *
 * @param data      json.parse
 * @param dataType  {} []
 * @param isCompel  
 */
function tryParseJson(data,dataType=[],isCompel=true) {
    let resData = null
    try {
        resData = JSON.parse(data)
    }
    catch (e) {
        console.log(''+e)
        resData = isCompel?dataType:data
    }
    return resData
}


export default {
    RC,
    call,
    isBlank,
    tryParseJson
}
