const commen = {}


/**
 *@tryParseJson json
 *
 * @param data      json.parse
 * @param dataType  {} []
 * @param isCompel  
 */
commen.tryParseJson = function(data,dataType=[],isCompel=true) {
    let resData = null
    try {
        resData = JSON.parse(data)
    }
    catch (e) {
        console.log(''+e)
        resData = isCompel?dataType:data
    }
    return resData
}


export default commen